:root{
    --grosorCajas: 150px;
}

.cajas {
    margin: 20px 0px;
    padding: 30px;
    width: var(--grosorCajas);
    height: var(--grosorCajas);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    border-radius: 10px;
    background-color: rgb(255,255,255, 0.90);
}

.cajas .caja {
    margin: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: linear-gradient(130deg, #FFBF69 0%, #FF9F1C 100%);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    -webkit-animation: pulse 1s linear infinite alternate;
    animation: pulse 1s linear infinite alternate;
}

.cajas .caja:nth-child(1) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.cajas .caja:nth-child(2) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.cajas .caja:nth-child(3) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.cajas .caja:nth-child(4) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.cajas .caja:nth-child(5) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}

.cajas .caja:nth-child(6) {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

.cajas .caja:nth-child(7) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
}

.cajas .caja:nth-child(8) {
    -webkit-animation-delay: 0.7s;
    animation-delay: 0.7s;
}

.cajas .caja:nth-child(9) {
    -webkit-animation-delay: 0.8s;
    animation-delay: 0.8s;
}

@-webkit-keyframes pulse {
    from, 40% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    80%, to {
        opacity: 1;
    }
}

@keyframes pulse {
    from, 40% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    80%, to {
        opacity: 1;
    }
}

@media screen and (max-width: 300px) {
    .caja {
        width: 30px !important;
        height: 30px !important;
    }
}